html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: var(--petal);
  color: var(--blackberry);
  font-family: var(--fontBody);
}

h1,
h2,
h3 {
  font-family: "Playfair Display", serif;
  font-weight: 800;
}

a,
a:visited,
a:active {
  color: var(--blackberry);
  text-decoration: none;
}

a:hover {
  color: var(--pinkGuava);
}

ul {
  list-style: none;
  margin: 0;
  text-align: center;
}

object {
  pointer-events: none;
}

address {
  all: inherit;
}

.grid--only {
  display: grid;
}

.grid {
  display: grid;
  align-items: center;
  justify-content: center;
}

.grid--start {
  display: grid;
  align-items: start;
  justify-content: center;
}

.grid--evenly {
  display: grid;
  align-content: space-evenly;
}

.grid-right {
  justify-content: right;
}

.grid--menu {
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
  align-content: end;
}

.grid--two-columns {
  grid-template-columns: repeat(2, 1fr);
}

.flex {
  display: flex;
}

.flex--apart {
  justify-content: space-between;
}

.flex--center {
  align-items: center;
}

.flex--bottom {
  align-items: flex-end;
}

.flex--baseline {
  align-items: baseline;
}

.upper {
  text-transform: uppercase;
}

.rounded-corners {
  border-radius: 50px;
}

.hide {
  display: none;
}

:root {
  --blackberry: hsl(266, 8%, 18%);
  --rusty: hsl(24, 74%, 51%);
  --orange: #FF7F50;
  --brightOrange:hsl(11, 80%, 60%);
  --brightRedPink: hsl(343, 100%, 39%);
  --brightPink: #DE3163;
  --pinkGuava: hsl(354, 63%, 60%);
  --peach: hsl(6.2,87.9%,93.5%);
  --petal: hsl(43, 100%, 99%);
  --shadow: rgba(45, 42, 49, 0.5);
}

:root {
  --fontDisplay: "Playfair Display", serif;
  --fontBody: "Montserrat", sans-serif;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.header__section {
  width: 90vw;
  height: 2.25rem;
}

.logo-icon {
  width: 0.875rem;
}

.header__menu--mobile {
  align-self: end;
  justify-self: end;
}

.mobile__menu-icon {
  cursor: pointer;
  text-align: right;
  display: block;
  position: relative;
  z-index: 9;
}

.line {
  background-color: var(--blackberry);
  width: 0.875rem;
  height: 0.125rem;
  display: block;
  justify-self: end;
  transition: all 0.375s ease-in-out;
}

.longer-line {
  width: 1.25rem;
  margin-bottom: 0.375rem;
}

.menu-toggle > span:first-child {
  transform: rotate(45deg);
}
.menu-toggle > span:last-child {
  width: 1.25rem;
  transform: translateY(-8px) rotate(-45deg);
}

.open {
  visibility: visible !important;
  opacity: 0.95 !important;
}

.nav__list {
  visibility: hidden;
  background-color: var(--peach);
  transform: translateY(0);
  transition: all 0.375s ease-in-out;
  opacity: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: auto;
  top: 0;
  left: 0;
  z-index: 1;
  font-size: 0.875rem;
  padding: 3.125rem 1.5625rem;
}

.nav__item--mobile {
  padding: 1.25rem 0;
}

.nav__item {
  font-size: 0.75rem;
  margin-left: 1.875rem;
}

.github-icon {
  font-size: 1rem;
}

@media (max-width: 48em) {
  .header__menu {
    display: none;
  }
}
@media (min-width: 48.0625em) {
  .header__section {
    width: 70vw;
    height: 2.5rem;
  }

  .mobile__menu-icon {
    display: none;
  }
}
.landing__wrapper {
  width: 100vw;
  justify-content: center;
}

.landing__text {
  text-align: center;
  align-self: space-between;
}

.landing__text--title {
  font-size: 2.125rem;
}

.landing__text--body {
  padding: 0 0.75rem;
  font-size: 0.75rem;
  line-height: 1.5;
}

.landing__card {
  width: 90vw;
  min-height: 70vh;
}

.landing__svg {
  width: 11.75rem;
  align-self: center;
  justify-self: center;
  transform: translateX(10%);
  margin-top: 7.5rem;
}

@media (min-width: 58em) {
  .landing__card {
    align-items: center;
    grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
    grid-template-areas: "text image";
    gap: 1.25rem;
    min-height: 70vh;
    width: 68vw;
  }

  .landing__card--half {
    height: 75%;
  }

  .landing__image {
    width: 100%;
    grid-area: image;
    align-content: start;
  }

  .landing__svg {
    width: 15.625rem;
    align-self: center;
    justify-self: center;
    transform: translateX(0);
    transform: translateY(25px);
  }

  .landing__text {
    text-align: left;
  }

  .landing__text--title {
    font-size: 3.625rem;
  }

  .landing__text--body {
    padding: 0 3.4375rem 0 0;
    font-size: 1rem;
    line-height: 1.5;
  }
}
button {
  font-family: inherit;
  /* For all browsers */
  line-height: 1.15;
  /* For all browsers */
  margin: 0;
  /* Firefox and Safari have margin */
  overflow: visible;
  /* Edge hides overflow */
  text-transform: none;
  /* Firefox inherits text-transform */
  -webkit-appearance: button;
  /* Safari otherwise prevents some styles */
}

button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring {
  outline: 1px dotted ButtonText;
}

.btn {
  font-family: inherit;
  font-size: 0.75rem;
  border: 0;
  border-radius: 3.125rem;
  background: linear-gradient(90deg, var(--brightOrange) 0%, var(--brightPink) 100%);
  color: var(--petal) !important;
  line-height: 1.2;
  letter-spacing: 1px;
  white-space: nowrap;
  text-decoration: none;
  padding: 0.875rem 1.75rem;
  cursor: pointer;
  transform: translateY(0);
  transition: transform 150ms, box-shadow 150ms;
}

.btn:hover {
  color: var(--petal);
  transform: translateY(-1px);
  box-shadow: rgba(45, 42, 49, 0.1) 0px 4px 6px -1px, rgba(45, 42, 49, 0.06) 0px 2px 4px -1px;
}

.btn-talk {
  margin-top: 1.25rem;
  justify-self: center;
  display: inline-block;
}

.btn-view {
  justify-self: center;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  display: inline-block;
}

.about-work__bkg {
  background-image: url("../../../assets/work-bkg-big.svg");
  background-position: 5% 0;
  background-size: cover;
  background-repeat: no-repeat;
  transform: rotate(4);
  width: 100vw;
  height: 43.75rem;
}

.about-work {
  width: 100vw;
  text-align: center;
  padding-top: 3.125rem;
}

.about-work__text--title {
  margin-bottom: 2.5rem;
  font-size: 1.5rem;
}

.work-icon {
  justify-self: center;
  transform: translateX(10%);
  width: 6.5625rem;
}

.about-work__text--subtitle {
  margin-bottom: 0.75rem;
  font-size: 1.25rem;
}

.about-work__text--body {
  font-size: 0.75rem;
  line-height: 1.6;
  padding: 0 2.5rem 1.25rem 2.5rem;
}

@media (min-width: 58em) {
  .about-work__bkg {
    background-image: url("../../../assets/work-bkg-big.svg");
    background-size: cover;
    background-repeat: no-repeat;
    transform: rotate(4);
    height: 56.25rem;
  }

  .about-work {
    transform: translateY(-90px);
  }

  .about-work__items {
    margin-top: 3.75rem;
  }

  .about-work__text--title {
    font-size: 2.625rem;
    margin-bottom: 3.75rem;
  }

  .about-work__items {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "left right";
    width: 60vw;
    gap: 20px;
    align-items: center;
  }

  .about-work__item {
    margin-top: 1.25rem;
  }

  .work-icon {
    width: 8.75rem;
  }

  .about-work__text--subtitle {
    font-size: 1.5rem;
  }

  .about-work__text--body {
    font-size: 0.875rem;
    line-height: 1.6;
    padding: 0 2.5rem 0 2.5rem;
  }
}
.projects-list__title {
  text-align: center;
  padding-top: 5.625rem;
  margin-bottom: 3.75rem;
  font-size: 1.5rem;
}

.projects-items {
  gap: 3.75rem;
}

.card {
  width: 85vw;
  border-radius: 1.25rem;
  background-color: var(--peach);
  overflow: hidden;
  box-shadow: rgba(45, 42, 49, 0.1) 0px 4px 6px -1px, rgba(45, 42, 49, 0.06) 0px 2px 4px -1px;
}

.card__half {
  height: 18.75rem;
  overflow: hidden;
}

.card__image {
  width: 100%;
}

.card-text__wrapper {
  padding: 0.625rem 1.25rem;
  line-height: 1.4;
  text-align: center;
  height: 100%;
}

.card__text--subtitle {
  font-size: 1.25rem;
}

.card__text--body {
  font-size: 0.75rem;
  line-height: 1.6;
}

.card__text--link {
  font-size: 0.75rem;
  display: block;
}

@media (min-width: 58em) {
  .projects-list__title {
    font-size: 2.625rem;
    margin-bottom: 7.5rem;
  }

  .card {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "left right";
    width: 60vw;
  }

  .card__half {
    min-height: 25rem;
  }

  .area-img {
    grid-area: right;
  }

  .card-text__wrapper {
    padding: 0.9375rem 3.75rem;
    text-align: left;
  }

  .card__text--subtitle {
    font-size: 1.5rem;
  }

  .btn-view {
    justify-self: left;
  }
}
.about-me {
  width: 87vw;
  text-align: center;
  padding-top: 5.625rem;
}

.about-me__title {
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 1.5rem;
}

.about-me__text--body {
  font-size: 0.75rem;
  line-height: 1.6;
  margin-top: 1.875rem;
  margin-bottom: 3.125rem;
}

.profile-pic {
  justify-self: center;
  width: 7.1875rem;
}

@media (min-width: 58em) {
  .about-me {
    width: 60vw;
  }

  .about-me__title {
    font-size: 2.625rem;
    margin-bottom: 5.625rem;
  }

  .about-me__text--body {
    font-size: 0.875rem;
    line-height: 1.6;
    padding: 0 2.5rem 0 2.5rem;
    margin-bottom: 5.625rem;
  }

  .profile-pic {
    width: 10rem;
    margin-bottom: 1.875rem;
  }
}
.footer__items {
  width: 87vw;
  text-align: center;
  padding-top: 3.125rem;
}

.footer__bkg {
  background-image: url("../../../assets/contact-bg.svg");
  background-position: 50% 0;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  height: 28.125rem;
}

.contact__title {
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 1.5rem;
}

.footer__nav {
  width: 85vw;
  justify-self: center;
  margin-top: 5.625rem;
}

.logo {
  width: 4.0625rem;
}

@media (min-width: 58em) {
  .footer_items {
    padding-top: 4.375rem;
  }

  .contact__title {
    font-size: 2.625rem;
  }

  .footer__bkg {
    height: 37.5rem;
  }

  .logo {
    width: 5.3125rem;
  }

  .footer__nav {
    width: 70vw;
    margin-top: 9.375rem;
  }
}